
html{font-size:15px
}
@media screen and (min-width: 480px){
html{font-size:16px
}
}
@media screen and (min-width: 640px){
html{font-size:17px
}
}
@media screen and (min-width: 1024px){
html{font-size:20px
}
}
p,li{font-size:15px
}
@media screen and (min-width: 480px){
p,li{font-size:16px
}
}
@media screen and (min-width: 640px){
p,li{font-size:17px
}
}
@media screen and (min-width: 1024px){
p,li{font-size:19px
}
}
h1{font-size:2.1rem
}
h2{font-size:1.6rem
}
h3{font-size:1.4rem
}
h4{font-size:1.2rem
}
h5{font-size:1.1rem
}
h1,h2,h3,h4,h5{margin:0.825rem 0
}
pre{margin:0.825rem 0;padding:1rem;font-size:0.8rem
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50
}
a{text-decoration:none
}
hr{margin:3.25em auto;border:0;border-top:1px solid #e4e7ec
}
.animated{animation-duration:2s;animation-fill-mode:both
}
.animated_5{animation-duration:0.8s;animation-fill-mode:both
}
.animated.delay{animation-delay:0.4s
}
@keyframes fadeIn{
from{opacity:0
}
to{opacity:1
}
}
.fadeIn{animation-name:fadeIn
}
@keyframes fadeInUp{
from{opacity:0;transform:translate3d(0,1%,0)
}
to{opacity:1;transform:none
}
}
.fadeInUp{animation-name:fadeInUp
}
@keyframes fadeInDown{
from{opacity:0;transform:translate3d(0,-100%,0)
}
to{opacity:1;transform:none
}
}
.fadeInDown{animation-name:fadeInDown
}
@keyframes fadeOut{
from{opacity:1
}
to{opacity:0
}
}
.fadeOut{animation-name:fadeOut
}
@keyframes fadeOutUp{
from{opacity:1
}
to{opacity:0;transform:translate3d(0, -100%, 0)
}
}
.fadeOutUp{animation-name:fadeOutUp
}
.fix{position:fixed;z-index:1;top:0;left:0
}



