
@import url(../../static/styles/tomorrow-night.css);
html{font-size:15px
}
@media screen and (min-width: 480px){
html{font-size:16px
}
}
@media screen and (min-width: 640px){
html{font-size:17px
}
}
@media screen and (min-width: 1024px){
html{font-size:20px
}
}
p,li{font-size:15px
}
@media screen and (min-width: 480px){
p,li{font-size:16px
}
}
@media screen and (min-width: 640px){
p,li{font-size:17px
}
}
@media screen and (min-width: 1024px){
p,li{font-size:19px
}
}
h1{font-size:2.1rem
}
h2{font-size:1.6rem
}
h3{font-size:1.4rem
}
h4{font-size:1.2rem
}
h5{font-size:1.1rem
}
h1,h2,h3,h4,h5{margin:0.825rem 0
}
pre{margin:0.825rem 0;padding:1rem;font-size:0.8rem
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50
}
a{text-decoration:none
}
hr{margin:3.25em auto;border:0;border-top:1px solid #e4e7ec
}
.article-container{width:100%;min-height:1500px
}
.article-container .article-intro{width:60%;margin:0 auto;padding-top:6.75rem;padding-bottom:4.25rem
}
.article-container .article-intro .article-title{font-size:1.8rem;text-align:center
}
.article-container .article-intro .article-title h1{display:inline-block;max-width:750px
}
.article-container .article-intro .article-author{margin-top:5.25rem;font-size:0.9rem
}
.article-container .article-intro .article-author em{color:#6d819c
}
.article-container .article-banner{margin-bottom:3rem
}
.article-container .article-banner img{width:70%
}
.article-container .article-content{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;width:60%;margin:0 auto;text-align:left
}
.article-container .article-content h5{font-size:1.2rem;margin:0.825rem 0
}
.article-container .article-content p{font-size:1rem;line-height:1.625rem;margin:0.625rem 0
}
.article-container .article-content blockquote{font-size:1.4rem;font-style:italic;margin-top:3rem;margin-bottom:3rem;color:#55967e;font-weight:bold
}
.article-container .article-content pre{white-space:pre-line;box-shadow:0 1px 2px rgba(0,0,0,0.125);border-radius:2px;line-height:1.1em;background-color:#f8f8f8;overflow-x:auto
}
.article-container .article-content code{line-height:1rem
}
.article-container hr{width:60%
}
@media screen and (max-width: 480px){
.article-container{min-height:600px
}
.article-container .article-intro{width:80%;padding-top:3.75rem;padding-bottom:3.25rem
}
.article-container .article-intro .article-author{margin-top:2.25rem
}
.article-container .article-banner{margin-bottom:2rem
}
.article-container .article-banner img{width:90%
}
.article-container .article-content{width:90%
}
.article-container hr{width:90%;margin:1rem auto
}
}



