
html{font-size:15px
}
@media screen and (min-width: 480px){
html{font-size:16px
}
}
@media screen and (min-width: 640px){
html{font-size:17px
}
}
@media screen and (min-width: 1024px){
html{font-size:20px
}
}
p,li{font-size:15px
}
@media screen and (min-width: 480px){
p,li{font-size:16px
}
}
@media screen and (min-width: 640px){
p,li{font-size:17px
}
}
@media screen and (min-width: 1024px){
p,li{font-size:19px
}
}
h1{font-size:2.1rem
}
h2{font-size:1.6rem
}
h3{font-size:1.4rem
}
h4{font-size:1.2rem
}
h5{font-size:1.1rem
}
h1,h2,h3,h4,h5{margin:0.825rem 0
}
pre{margin:0.825rem 0;padding:1rem;font-size:0.8rem
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50
}
a{text-decoration:none
}
hr{margin:3.25em auto;border:0;border-top:1px solid #e4e7ec
}
.archive-container{min-height:400px
}
.archive-container h2{margin-top:2em;font-size:40px
}
.archive-container .archive-item .archive-year-list{list-style:none;padding:0;width:70%;margin:0 auto
}
.archive-container .archive-item .archive-year-list .article-item{margin:10px 0;font-size:18px
}
.archive-container .archive-item .archive-year-list .article-item h4{margin:0;cursor:pointer;display:inline-block;color:#55967e
}
.archive-container .archive-item .archive-year-list .article-item h4:hover{color:#2c3e50
}
.archive-container .archive-item .archive-year-list .article-item .post-info{font-size:16px
}
.archive-container .archive-item .archive-year-list hr{width:30%
}



